import React, { useState, useEffect } from "react";
import  NavBar  from "../../components/navigationbar/index.js";
import  CustomTable  from "../../components/customtable/index.js";
import { Container, Row, Col, Table, Image } from 'react-bootstrap';
import { useTable } from 'react-table';
import { Bar, Doughnut } from 'react-chartjs-2';

function Files() {
  const [error,setError] = useState(null);
  const [isLoaded,setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);
  const [datag1, setDatag1] = useState([]);
  const [datag2, setDatag2] = useState([]);
  const [tableInstance,setTableInstance] = useState([]);
  const [data1,setData1] = useState({});
  const [data2,setData2] = useState({});
  const columns = React.useMemo(() => [{Header:"Resumen",accessor: "overview"},{Header:"Institución",accessor: "office"},{Header:"Fecha",accessor: "start"}],[]);

  //Se cargan los datos del API de ALAC
  useEffect(() => {
    fetch("https://alac.funde.org/api/v1/requests?startdate=2010-01-01&enddate=2022-12-31&page=0&limit=25")
    .then(res => res.json())
    .then(
      (result) => {
        setItems(result);
      },
      (error) => {
        setError(error);
      }
    );
    fetch("https://alac.funde.org/api/v1/requeststatistics/1")
    .then(res => res.json())
    .then(
      (result) => {
        setDatag1(result);
        setIsLoaded(true);
      },
      (error) => {
        setError(error);
      }
    );
    fetch("https://alac.funde.org/api/v1/requeststatistics/2")
    .then(res => res.json())
    .then(
      (result) => {
        setDatag2(result);

      },
      (error) => {
        setIsLoaded(true);
        setError(error);
      }
    );
  },[]);

  useEffect(() => {
    console.log(datag1);
    console.log(datag2);
    if (isLoaded){
      setData1(
      {
        labels: datag1.map(row => {return row['institution']}),
        datasets: [
            {
              label: 'Top de solicitudes por institución',
              backgroundColor: 'rgba(30, 66, 118,0.2)',
              borderWidth: 1,
              hoverBackgroundColor: 'rgba(30, 66, 118,0.5)',
              data: datag1.map(row => {return row['frequency']})
            }
          ]
        }
      );

      setData2(
          {
          	labels: datag2.map(row => {return row['_id']}),
          	datasets: [
              {
            		data: datag2.map(row => {return row['frequency']}),
            		backgroundColor: 'rgba(30, 66, 118,0.2)',
            		hoverBackgroundColor: 'rgba(30, 66, 118,0.5)',
          	 }
          ]
        }
      );
    }
    console.log(datag1);
    console.log(datag2);
  },[isLoaded]);

  if (error){
    return <div>No se pudieron cargar los datos. Consulta con el personal de ALAC para más datos.</div>
  } else if (!isLoaded) {
    return <div>Cargando...</div>
  } else {
    return (
      <div>
        <Container>
        <NavBar />
          <Row style={{paddingTop:"20px"}}>
            <Col sm>
              <div className="my-auto text-center">
                <h2>Listado de solicitudes</h2>
              </div>
            </Col>
          </Row>
          {/*Apartado inicial con datos generales*/}
          <Row>
            <Col sm>
            <p style={{columnCount:"2", columnWidth:"170px",columnRule:"1px solid #bbb", paddingTop:"20px"}}>Aquí tienes el listado de solicitudes de acceso
            a la información realizadas por ALAC. Cada expediente contiene un resúmen y los documentos asociados, para que los
            descargues. Con los botones que tienes abajo puedes filtrar los resultados para acercarte a
            aquellos que sean de tú interés. También hay unos gráficos que resumen el trabajo de ALAC en los últimos años.</p>
            </Col>
          </Row>
          {/*Gráficos consolidados de las peticiones*/}
          <Row style={{paddingTop:"20px"}}>
            <Col sm={6}>
              <Bar
                data={data1}
                width={100}
                height={150}
                options={{ maintainAspectRatio: false }}
              />
            </Col>
            <Col sm={6}>
              <p className="my-auto text-center" style={{fontSize:"10px"}}>Solicitudes por año</p>
              <Doughnut data={data2} />
            </Col>
          </Row>
          {/*Listado de casos*/}
          <Row style={{paddingTop:"40px"}}>
            <Col sm>
              <CustomTable columns={columns} data={items} />
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
export default Files;
