import React, { useState, useEffect } from "react";
import  NavBar  from "../../components/navigationbar/index.js";
import { Container, Row, Col, Form, Tab, Nav, Button } from 'react-bootstrap';


function ContactUs() {
  return(
    <>
    <Container>
      <NavBar />
      <Row style={{paddingTop:"20px"}}>
        <Col sm>
        <div className="my-auto text-center">
          <h2>Asesoramiento y seguimiento a casos</h2>
        </div>
        </Col>
      </Row>
      <Row>
        <Col sm>
        <p style={{columnCount:"2", columnWidth:"170px",columnRule:"1px solid #bbb", paddingTop:"20px"}}>ALAC te brinda gratuitamente el servicio de asesoria para
        indicarte cómo puedes hacer para pedir información. También recibimos y damos seguimiento a casos de corrupción; si tienes información
        que nos sea útil, puedes enviarla de forma anónima. A la izquierda tienes dos opciones: una para pedir asesoría y otra para dar seguimiento
        a un caso ya presentado.</p>
        </Col>
      </Row>
      <Tab.Container id="left-tabs-example" defaultActiveKey="first">
        <Row style={{paddingTop:"20px"}}>
          <Col sm={3}>
            <Nav variant="pills" className="flex-column">
              <Nav.Item>
                <Nav.Link eventKey="first">Clic aquí si quieres asesoría o denunciar un caso de corrupción</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="second">Presiona aquí si quieres saber cómo va tu caso o denuncia</Nav.Link>
              </Nav.Item>
            </Nav>
          </Col>
          <Col sm={9}>
            <Tab.Content>
              <Tab.Pane eventKey="first">
                <Form>
                  <Form.Group controlId="exampleForm.ControlTextarea1">
                    <Form.Label>Ingresa el texto de tu denuncia o una breve explicación de en qué requieres ayudar.</Form.Label>
                    <Form.Control as="textarea" rows="4" />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput1">
                    <Form.Label>Dirección de correo electrónico</Form.Label>
                    <Form.Control type="email" placeholder="tunombre@gmail.com" />
                  </Form.Group>
                  <Button variant="primary">Enviar mi texto</Button>{' '}
                </Form>
                </Tab.Pane>
                <Tab.Pane eventKey="second">
                <Form>
                  <Form.Group controlId="formGridAddress1">
                    <Form.Label>Año</Form.Label>
                    <Form.Control placeholder="2020" />
                  </Form.Group>
                  <Form.Group controlId="formGridAddress1">
                    <Form.Label>Número de ticket</Form.Label>
                    <Form.Control placeholder="960" />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput1">
                    <Form.Label>Dirección de correo electrónico</Form.Label>
                    <Form.Control type="email" placeholder="tunombre@gmail.com" />
                  </Form.Group>
                  <Button variant="primary">Enviar mi texto</Button>{' '}
                </Form>
              </Tab.Pane>
            </Tab.Content>
          </Col>
        </Row>
        </Tab.Container>
    </Container>
    </>
  );
}
export default ContactUs;
