import React from "react";
import { Col, Row, Table, Button, Modal } from 'react-bootstrap';

function CustomModal({ show, onHide, data, headers }) {
  return (
    <>
    <Modal show={show} onHide={onHide} size="lg">
      <Modal.Header closeButton>
          <Modal.Title>Datos del expediente</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Table responsive="sm">
            {Object.keys(headers).map(header => {
              if (!Array.isArray(data[header])){
                return (
                  <tr>
                    <td>{headers[header]}:</td>
                    <td>{data[header]}</td>
                  </tr>
                )
              } else {
                return (<tr><td>{headers[header]}:</td><td>
                    <ul>{data[header].map(row => <li>{row.date}:{row.detail}</li>)}</ul>
                  </td></tr>)
              }

            })}
            <tr>
              <td>Documentos:</td>
              <td><ul>{data["documents"].length > 0 &&
              data["documents"].map(document => {
                return (
                    <li>{document["date"]}:<a href={document["path"]} target="_blank">{document["title"]}</a></li>
                )
              })
            }</ul></td>
            </tr>
          </Table>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={onHide}>
            Cerrar
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );

}
export default CustomModal;
