import React, { useState } from "react";
import { Col, Row, Table, Form, Button, Modal, Pagination  } from 'react-bootstrap';
import { useTable, usePagination, useGlobalFilter, useAsyncDebounce } from 'react-table';
import matchSorter from 'match-sorter';
import {ReactComponent as Plus} from '../../images/plus.svg';
import  CustomModal  from "../../components/custommodal/index.js";


function CustomTable({ columns, data }) {
  const [show, setShow] = useState(false);
  const [id, setId] = useState("");
  const handleClose = () => setShow(false);
  const handleShow = (id) => {setId(id);setShow(true);};

  // Define a default UI for filtering
  function GlobalFilter({
    preGlobalFilteredRows,
    globalFilter,
    setGlobalFilter,
  }) {
    const count = preGlobalFilteredRows.length
    const [value, setValue] = React.useState(globalFilter)
    const onChange = useAsyncDebounce(value => {
      setGlobalFilter(value || undefined)
    }, 200)
    return (
      <span>
        <Form.Control type="text" value={value || ""}
        onChange={e => {
          setValue(e.target.value);
          onChange(e.target.value);
        }}
        placeholder={`${count} registros seleccionados...`}

        />
      </span>
    )
  }

  function fuzzyTextFilterFn(rows, id, filterValue) {
    return matchSorter(rows, filterValue, { keys: [row => row.values[id]] })
  }

  // Let the table remove the filter if the string is empty
  fuzzyTextFilterFn.autoRemove = val => !val

  function TableUI({ columns, data }){

      const filterTypes = React.useMemo(
      () => ({
        // Add a new fuzzyTextFilterFn filter type.
        fuzzyText: fuzzyTextFilterFn,
        // Or, override the default text filter to use
        // "startWith"
        text: (rows, id, filterValue) => {
          return rows.filter(row => {
            const rowValue = row.values[id]
            return rowValue !== undefined
              ? String(rowValue)
                  .toLowerCase()
                  .startsWith(String(filterValue).toLowerCase())
              : true
          })
        },
      }),
      []
    )

    const {getTableProps,getTableBodyProps, headerGroups, prepareRow,
      page,
      canPreviousPage,
      canNextPage,
      pageOptions,
      pageCount,
      gotoPage,
      nextPage,
      previousPage,
      setPageSize,
      state,
      setGlobalFilter,
      preGlobalFilteredRows,
      visibleColumns,
      state: {pageIndex,pageSize},

    } = useTable({
      columns,
      data, initialState: {pageIndex: 0} },
      useGlobalFilter,
      usePagination,
       );

    return (
      <>
      <Row style={{paddingBottom:"20px"}}>
        <Col sm={4}>
        <Form.Control as="select"
            value={pageSize}
            onChange={(e) => {
              setPageSize(Number(e.target.value));
            }}
          >
            {[10, 20, 30, 40, 50].map((pageSize) => (
              <option key={pageSize} value={pageSize}>
                Mostrar {pageSize} filas
              </option>
            ))}
          </Form.Control>
        </Col>
        <Col sm={8}>
          <GlobalFilter
            preGlobalFilteredRows={preGlobalFilteredRows}
            globalFilter={state.globalFilter}
            setGlobalFilter={setGlobalFilter}
          />
        </Col>
      </Row>

      <Table {...getTableProps()} responsive="sm">
        <thead>
          {headerGroups.map(headerGroup => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map(column => (
                <th {...column.getHeaderProps()}>{column.render("Header")}</th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {page.map((row, i) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell,j) => {
                  if (j == 0) return <td {...cell.getCellProps()}><Plus onClick={() => handleShow(cell.row.original._id)} fill='#1e4276' style={{height: 15}} />{cell.render("Cell")}</td>;
                  else return <td {...cell.getCellProps()}>{cell.render("Cell")}</td>;
                })}
              </tr>
            );
          })}
        </tbody>
      </Table>
      <Pagination>
        <Pagination.First onClick={() => gotoPage(0)} disabled={!canPreviousPage} />
        <Pagination.Prev onClick={() => previousPage()} disabled={!canPreviousPage} />
        <Pagination.Next onClick={() => nextPage()} disabled={!canNextPage} />
        <Pagination.Last onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage} />
        <span>Página {pageIndex + 1} de {pageOptions.length}</span>
      </Pagination>
      </>
    );
  }

  return (
    <>
    <TableUI columns={columns} data={data} />
    {id != '' &&
      <CustomModal show={show}
      onHide={handleClose}
      data={data.filter(register => register._id == id)[0]}
      headers={{status:"Estado",office:"Institución",overview:"Resumen",detail:"Detalle",
      start:"Fecha de inicio",
      finish: "Fecha de finalización",
      updates: "Actualizaciones",
      }} />
    }
    </>
  );
}
export default CustomTable;
