import React from "react";
import  NavBar  from "../../components/navigationbar/index.js";
import  Footer  from "../../components/footer/index.js";
import { Container, Row, Col, Image } from 'react-bootstrap';
import logo from '../../images/alac-logo.png';
import {ReactComponent as Police} from '../../images/policeman.svg';
import {ReactComponent as Files} from '../../images/document.svg';
import {ReactComponent as Megaphone} from '../../images/megaphone.svg';

function Home() {
  return (
    <div>
      <Container>
        <NavBar />
        <Row>
          <Col sm>
          <div class="my-auto text-center">
            <br />
            <Image src={logo} style={{width:"40%", height:"40%"}} />
            <br />
          </div>
          </Col>
        </Row>
        <Row>
          <Col sm>
          <p style={{columnCount:"2", columnWidth:"170px",columnRule:"1px solid #bbb", paddingTop:"30px"}}>Bienvenido al sitio web de ALAC.
           Te brindamos asesoría legal, gratuita y confidencial, para denunciar casos de corrupción y solicitar información pública.
           En este sitio también ponemos a tu disposición los documentos que hemos obtenido, a partir de solicitudes realizadas a
           instituciones públicas, además de las denuncias que hemos realizado hasta la fecha.</p>
          </Col>
        </Row>
        <Row style={{paddingTop:"30px"}}>
          <Col sm={4}>
            <div className="card" style={{height: 400}}>
              <Police fill='#1e4276' style={{height: 100,paddingTop:"10px"}} />
              <div className="card-body">
                <h5 className="card-title">¿Quieres denunciar corrupción o solicitar información pública?</h5>
                <p className="card-text">Te brindamos asesoría gratuita para denunciar un acto de corrupción o para ejercer tu derecho de acceso a la información</p>
                <a href="#/asesoria" style={{backgroundColor: "#1e4276", borderColor: "#1e4276"}} className="btn btn-primary">¡Pedir asesoría!</a>
              </div>
            </div>
          </Col>
          <Col sm={4}>
            <div className="card" style={{height: 400}}>
              <Files fill='#1e4276' style={{height: 100,paddingTop:"10px"}} />
              <div className="card-body">
                <h5 className="card-title">¿Buscas información de alguna entidad pública?</h5>
                <p className="card-text">Ponemos a tu disposición la lista de expedientes con la información que ALAC ha solicitado a diferentes entidades</p>
                <a href="#/solicitudes" style={{backgroundColor: "#1e4276", borderColor: "#1e4276"}} className="btn btn-primary">¡Ver documentos!</a>
              </div>
            </div>
          </Col>
          <Col sm={4}>
            <div className="card" style={{height: 400}}>
              <Megaphone className="card-img-top" fill='#1e4276' style={{height: 100,paddingTop:"10px"}} />
              <div className="card-body">
                <h5 className="card-title">¿Te interesa conocer las denuncias que hemos presentado?</h5>
                <p className="card-text">En este apartado puedes consultar las denuncias que hemos interpuesto desde ALAC</p>
                <a href="#/denuncias" style={{backgroundColor: "#1e4276", borderColor: "#1e4276"}} className="btn btn-primary">¡Conocer las denuncias!</a>
              </div>
            </div>
          </Col>
        </Row>
      <Footer />
      </Container>
    </div>
  );
}
export default Home;
