import React from "react";
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';


function NavBar() {
  return (
    <Navbar collapseOnSelect expand="lg" style={{backgroundColor: "#1e4276"}} variant="dark">
      <Navbar.Brand href="#/">ALAC</Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Link href="#/solicitudes">Solicitudes</Nav.Link>
          <Nav.Link href="#/denuncias">Denuncias</Nav.Link>
          <Nav.Link href="#/asesoria">Pedir asesoria</Nav.Link>
          <NavDropdown title="Otros enlaces" id="collasible-nav-dropdown">
            <NavDropdown.Item href="#/faq">¿Quienes somos?</NavDropdown.Item>
            <NavDropdown.Item href="#/api">API</NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item href="https://www.funde.org">FUNDE</NavDropdown.Item>
          </NavDropdown>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
}
export default NavBar;
