import React from "react";
import { Navbar, Nav, NavDropdown, Col, Row } from 'react-bootstrap';
import logo from '../../images/logofunde.png';

function Footer() {
  return (
    <div style={{paddingTop:"20px"}}>
      <hr />
      <Row>
        <Col sm>
          <img src={logo} alt="Logo" style={{width:"100%", height:"100%"}} />
        </Col>
        <Col sm>
          <p>ALAC es un servicio de la <a href="http://funde.org">Fundación Nacional para el Desarrollo (FUNDE)</a>, Capítulo
          de Transparencia Internacional en El Salvador</p>
        </Col>
        <Col sm>
          <p>Para más información, puedes escribir al número de Whatsapp +503 7987-1688 o escribir al correo alac@funde.org</p>
        </Col>
      </Row>
    </div>
  );
}
export default Footer;
